import express from 'express';
import path from 'path';
import fs from 'fs';
import {Client as SQLClient} from '../packages/db/client';

const router = express.Router();

interface Cour {
    id: number;
    type_cours: string;
    date_cours: string;
    heure_debut: string;
    heure_fin: string;
    participants?: Participant[];
}
  
interface Participant {
    participant_id: number;
    first_name: string;
    last_name: string;
    email: string;
}


// Endpoint pour la v√©rification de l'utilisateur
router.get('/', async (req, res) => {
  // Cr√©er une instance du client SQL
  const client = new SQLClient();

  try {
    // Premi√®re requ√™te : r√©cup√©rer les cours √† venir
    const queryCours = 'SELECT * FROM cours WHERE date_cours >= CURDATE() LIMIT 12';
    const coursResults: Cour[] = await client.query(queryCours); // R√©cup√®re tous les cours √† venir

    if (coursResults.length === 0) {
      return res.status(404).send('Aucun cours trouv√©.');
    }

    // Retourner la liste des cours
    res.json({ cours: coursResults });

  } catch (err) {
    console.error('Erreur lors du chargement des cours:', err);
    res.status(500).send('Erreur lors du chargement des cours.');
  }
});

  
  
  

router.post('/participant', async (req, res) => {
  const { cour_id } = req.body; // R√©cup√©rer l'ID du cours depuis le corps de la requ√™te
  console.log(cour_id);

  if (!cour_id) {
    return res.status(400).json({ message: "L'ID du cours est requis." });
  }

  try {
    // Cr√©er une instance du client SQL (en supposant que tu utilises mysql2 ou mysql)
    const client = new SQLClient(); // Assure-toi que ce client est bien configur√© pour ta base de donn√©es

    // Requ√™te SQL pour r√©cup√©rer les participants inscrits √† ce cours
    const query = `
      SELECT utilisateurs.id AS participant_id, utilisateurs.last_name, utilisateurs.first_name, utilisateurs.email
      FROM utilisateurs
      JOIN inscriptions ON utilisateurs.id = inscriptions.utilisateur_id
      WHERE inscriptions.cours_id = ?;
    `;

    // Ex√©cuter la requ√™te avec l'ID du cours
    const results = await client.query(query, [cour_id]);

    // V√©rifier si les r√©sultats sont valides
    if (!results || results.length === 0) {
      return res.status(404).json({ message: "Aucun participant trouv√© pour ce cours." });
    }

    // Utilisation s√©curis√©e de 'participants'
    const participants = results;

    console.log(participants);

    // R√©ponse avec les participants trouv√©s
    res.status(200).json({ participants: participants, message: "Participants trouv√©s avec succ√®s." });

  } catch (error) {
    console.error('Erreur lors de la r√©cup√©ration des participants:', error);
    return res.status(500).json({ message: "Erreur serveur lors de la r√©cup√©ration des participants." });
  }
});



router.post('/inscription', async (req, res) => {
  const { last_name, first_name, email } = req.body.user;
  const { id: coursId } = req.body.cours;

  if (!last_name || !first_name || !email || !coursId) {
    return res.status(400).json({ message: "Des informations sont manquantes." });
  }

  try {
    const client = new SQLClient();

    // Requ√™te pour r√©cup√©rer l'ID de l'utilisateur
    const queryUser = `
      SELECT id FROM utilisateurs 
      WHERE last_name = ? AND first_name = ? AND email = ?;
    `;
    const resultUser = await client.query(queryUser, [last_name, first_name, email]);

    if (resultUser.length === 0) {
      return res.status(404).json({ message: "Utilisateur non trouv√©." });
    }

    const userId = resultUser[0].id;

    // V√©rification si l'utilisateur est d√©j√† inscrit au cours
    const queryCheck = `
      SELECT * FROM inscriptions 
      WHERE utilisateur_id = ? AND cours_id = ?;
    `;
    const resultCheck = await client.query(queryCheck, [userId, coursId]);

    console.log(resultCheck)

    if (resultCheck.length > 0) {
      // Informer que l'utilisateur est d√©j√† inscrit
      console.log(`Utilisateur d√©j√† inscrit au cours ${coursId}`);
      return res.status(409).json({ info_message: `Vous √™tes d√©j√† inscrit au cours ${coursId}` });
    }

    // Inscription de l'utilisateur
    const queryInsert = `
      INSERT INTO inscriptions (utilisateur_id, cours_id) 
      VALUES (?, ?);
    `;
    await client.query(queryInsert, [userId, coursId]);

    // R√©ponse de succ√®s
    res.status(200).json({ success_message: "Inscription r√©ussie", userId, coursId });

  } catch (error) {
    console.error('Erreur lors de l\'inscription:', error);
    res.status(500).json({ message: "Erreur serveur lors de l'inscription." });
  }
});



// Route pour valider la pr√©sence d'un participant
router.post('/participant/cancel', async (req, res) => {
  const { courId, participantId } = req.body; // R√©cup√®re les donn√©es envoy√©es par le front
  
  if (!courId || !participantId) {
    return res.status(400).json({ message: 'Cour ID et Participant ID sont requis.' });
  }

  try {
    const client = new SQLClient();
    // Ex√©cution de la requ√™te d'update avec les param√®tres
    const result = await client.query(
      'UPDATE inscriptions SET status = FALSE WHERE cours_id = ? AND utilisateur_id = ?',
      [courId, participantId]
    );

    // V√©rifie si des lignes ont √©t√© r√©ellement modifi√©es
    if (result.changedRows === 0) {
      return res.status(404).json({ message: 'Aucun changement n\'a eu lieu, v√©rifie si le participant est d√©j√† valid√©.' });
    }

    // Si un changement a eu lieu
    return res.status(200).json({ message: 'Participant supprim√© avec succ√®s.' });
  } catch (error) {
    console.error('Erreur lors de la mise √† jour du statut :', error);
    return res.status(500).json({ message: 'Erreur serveur.' });
  }
});

router.post('/participant/validation', async (req, res) => {
  const { courId, participantId } = req.body; // R√©cup√®re les donn√©es envoy√©es par le front
  
  if (!courId || !participantId) {
    return res.status(400).json({ message: 'Cour ID et Participant ID sont requis.' });
  }

  try {
    const client = new SQLClient();
    // Ex√©cution de la requ√™te d'update avec les param√®tres
    const result = await client.query(
      'UPDATE inscriptions SET status = TRUE WHERE cours_id = ? AND utilisateur_id = ?',
      [courId, participantId]
    );

    // V√©rifie si des lignes ont √©t√© r√©ellement modifi√©es
    if (result.changedRows === 0) {
      return res.status(404).json({ message: 'Aucun changement n\'a eu lieu, v√©rifie si le participant est d√©j√† valid√©.' });
    }

    // Si un changement a eu lieu
    return res.status(200).json({ message: 'Participant valid√© avec succ√®s.' });
  } catch (error) {
    console.error('Erreur lors de la mise √† jour du statut :', error);
    return res.status(500).json({ message: 'Erreur serveur.' });
  }
});








  







export default router;