import express from 'express';
import path from 'path';
import fs from 'fs';
import {Client as SQLClient} from '../packages/db/client';

const router = express.Router();



router.post('/', async (req, res) => {
    const utilisateurs = req.body; 
    const client = new SQLClient(); 

    try {
        for (const utilisateur of utilisateurs) {
            const { nom, email, cours } = utilisateur;

            // V√©rifier si l'utilisateur existe d√©j√†
            const userQuery = `SELECT * FROM utilisateurs WHERE email = ?`;
            const userValues = [email];
            const existingUser = await client.query(userQuery, userValues);

            if (existingUser.length > 0) {
                console.log(`L'utilisateur avec l'email ${email} existe d√©j√†.`);
                return res.status(400).json({ message: `L'utilisateur avec cet email existe d√©j√†.` });
            }

            // Ins√©rer l'utilisateur
            const insertUserQuery = `INSERT INTO utilisateurs (first_name, email, status) VALUES (?, ?, 'visitor')`;
            const userResult = await client.query(insertUserQuery, [nom, email]);
            const utilisateurId = userResult.insertId; // R√©cup√©rer l'ID de l'utilisateur

            console.log("Utilisateur ins√©r√© avec succ√®s, ID:", utilisateurId);


            for (const cour of cours) {
                const { id: coursId } = cour; 

                // V√©rifier si l'utilisateur a d√©j√† r√©serv√© ce cours
                const checkQuery = `
                    SELECT * FROM reservations 
                    WHERE utilisateur_id = ? AND cours_id = ?
                `;
                const checkValues = [utilisateurId, coursId];
                const existingReservation = await client.query(checkQuery, checkValues);

                if (existingReservation.length > 0) {
                    console.log(`L'utilisateur ID: ${utilisateurId} a d√©j√† r√©serv√© ce cours ID: ${coursId}`);
                    return res.status(400).json({ message: `L'utilisateur a d√©j√† r√©serv√© ce cours.` });
                }

                // Cr√©er la r√©servation
                await client.query(
                    'INSERT INTO reservations (utilisateur_id, cours_id) VALUES (?, ?)', 
                    [utilisateurId, coursId]
                );

                console.log(`R√©servation cr√©√©e pour l'utilisateur ID: ${utilisateurId} et le cours ID: ${coursId}`);
            }

            // V√©rifier le statut de l'utilisateur apr√®s insertion
            const statusQuery = `SELECT status FROM utilisateurs WHERE id = ?`;
            const statusResult = await client.query(statusQuery, [utilisateurId]);
            const userStatus = statusResult[0] ? statusResult[0].status : null; // Obtenir le statut
            res.status(200).json({ message: 'Utilisateurs et r√©servations cr√©√©s avec succ√®s.', role: userStatus });
        }

    } catch (error) {
        console.error('Erreur lors de la cr√©ation des utilisateurs et r√©servations:', error);
        res.status(500).json({ error: 'Erreur serveur lors de la cr√©ation.' });
    } finally {
        // Optionnel : fermer la connexion √† la base de donn√©es si n√©cessaire
        // await client.close();
    }
});




router.post('/verification', async (req, res) => {
    const { nom, email } = req.body;
    console.log("Nom:", nom, "Email:", email);
    console.log("V√©rification en cours...");

    const client = new SQLClient(); 

    try {
        // √âtape 1: V√©rifier si l'utilisateur existe
        const userQuery = `
            SELECT * FROM utilisateurs 
            WHERE email = ?
        `;
        const userValues = [email];
        const existingUser = await client.query(userQuery, userValues);

        // √âtape 2: Si l'utilisateur n'existe pas, permettre l'inscription
        if (existingUser.length === 0) {
            console.log(`L'utilisateur ${nom} (${email}) n'est pas inscrit.`);
            return res.status(200).json({ canRegister: true }); // Indique que l'utilisateur peut s'inscrire
        }

        // √âtape 3: Si l'utilisateur existe, v√©rifier les r√©servations
        const utilisateurId = existingUser[0].id; 
        const reservationQuery = `
            SELECT * FROM reservations 
            WHERE utilisateur_id = ?
        `;
        const reservationValues = [utilisateurId];
        const reservations = await client.query(reservationQuery, reservationValues);
        
        // √âtape 4: Si l'utilisateur a d√©j√† r√©serv√©, renvoyer un message d'erreur
        if (reservations.length > 0) {
            console.log(`L'utilisateur ${nom} (${email}) a d√©j√† r√©serv√© un cours.`);
            return res.status(400).json({ message: "Vous avez d√©j√† r√©serv√© un cours d'essai.", canRegister: false }); // Indique que l'utilisateur ne peut pas s'inscrire
        }

        // √âtape 5: Aucune r√©servation existante
        res.status(200).json({ message: "Aucune r√©servation existante pour cet utilisateur.", canRegister: true }); // Indique que l'utilisateur peut r√©server
        
    } catch (error) {
        console.error('Erreur lors de la v√©rification des r√©servations:', error);
        res.status(500).json({ error: 'Erreur serveur lors de la v√©rification.' });
    } finally {
       
    }
});











export default router;