import MysqlConnector from './mysqlconnector';


export class Client{

    VerificationUtilisateur(sql: string, values: any[]): Promise<any> {
        return new Promise((resolve, reject) => {
            const mysqlConnector = new MysqlConnector();
            console.log("√©x√©cution du query");
            console.log(sql,values);
            mysqlConnector.query(sql, values, (error, results) => {
                if (error) {
                    console.error('Erreur lors de l\'ex√©cution de la requ√™te : ' + error.message);
                    reject(error);
                } else {
                    console.log('R√©sultats de la requ√™te :', results);
                    resolve(results);
                }
    
                // Fermez la connexion ici apr√®s avoir trait√© les r√©sultats
                mysqlConnector.close();
            });
        });
    }

    Insert(sql: string, values: any[]): Promise<any> {
        return new Promise((resolve, reject) => {
            const mysqlConnector = new MysqlConnector();
            console.log("√©x√©cution du query Insert");
            mysqlConnector.query(sql, values, (error, results) => {
                if (error) {
                    console.error('Erreur lors de l\'ex√©cution de la requ√™te : ' + error.message);
                    reject(error);
                } else {
                    console.log('R√©sultats de la requ√™te :', results);
                    resolve(results);
                }
    
                // Fermez la connexion ici apr√®s avoir trait√© les r√©sultats
                mysqlConnector.close();
            });
        });
    }

    query(sql: string, values?: any[]): Promise<any> {
        return new Promise((resolve, reject) => {
            const mysqlConnector = new MysqlConnector();
            console.log("√©x√©cution du query");
            mysqlConnector.query(sql, values as any, (error, results) => {
                if (error) {
                    console.error('Erreur lors de l\'ex√©cution de la requ√™te : ' + error.message);
                    reject(error);
                } else {
                    console.log('R√©sultats de la requ√™te :', results);
                    resolve(results);
                }
    
                // Fermez la connexion ici apr√®s avoir trait√© les r√©sultats
                mysqlConnector.close();
            });
        });
    }

}


